import React from "react"
import { Link } from "gatsby"
import Typography from 'typography'

const typography = new Typography({
  baseFontSize: '18px',
  baseLineHeight: 1.666,
  headerFontFamily: ['Avenir Next', 'Helvetica Neue', 'Segoe UI', 'Helvetica', 'Arial', 'sans-serif'],
  bodyFontFamily: ['Segoe UI', 'serif'],
  // See below for the full list of options.
})
typography.injectStyles();

const ListLink = props => (  
	<li className="menuLink">    
	<Link to={props.to}>{props.children}</Link>  </li>
)

export default ({ children }) => (
  <div>
     <header>     
        <ul className="menu">        
          <ListLink to="/">Home</ListLink>      
          <ListLink to="/post/">Posts</ListLink>       
          <ListLink to="/about/">About</ListLink>       
        </ul>    
     </header>
      <div className="content"> 
      <div
          style={{
            margin: '0 auto',
            maxWidth: 960,
            padding: '0px 1.0875rem 1.45rem',
            paddingTop: 0,
          }}
        >    
        {children}
        </div>
      </div>
      <footer>
          <ul>
            <li><a href="https://github.com/dattatrayam"><span>Github</span></a></li>
            <li><a href="https://www.linkedin.com/in/dattatraya-mule-733033b/"><span>Linkedin</span></a></li>
            <li><a href="https://eportfolio.greatlearning.in/dattatraya-mule"><span>ePortfolio</span></a></li>
          </ul>
      </footer>

  </div>
)